@import '../variables.scss';

.slick-dots{
    list-style: none;
    display: flex !important;
}

.slick-dots{
    li{
        margin-right: 30px;
    }
}

.slick-dots button{
    width: 25px;
    height: 25px;
    font-size: 0;
    border: none !important;
    border-radius: 50%;
    background-color: #E4E4E4;
}

.slick-dots .slick-active button{
    background-color: $primary;
}

.slick-slide{
    margin: 0 20px;
}

.slick-list{
    margin: 0 -20px;
}

.slider{
    position: relative;
    display: flex;
    align-items: center;
}

label>input[type="radio"] {
    display: none;
}

label>input[type="radio"]+*::before {
    content: "";
    display: inline-block;
    vertical-align: bottom;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #E4E4E4;
}

label>input[type="radio"]:checked+*::before {
    background-color: $primary;
}

label>input[type="radio"]+* {
    display: inline-block;
    margin-right: 16px;
    font-size: 0;
}



@media screen and (min-width: 768px) {
    label>input[type="radio"]+*::before{
        height: 25px;
        width: 25px;
    }
    label>input[type="radio"]+*{
        margin-right: 30px;
    }
}

.carousel-container{
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: hidden;
}

.carousel{
    display: grid;
    align-items: center;
    column-gap: 20px;
}


.carousel-catalog-inputs{
    display: block;
    margin: 0 auto;
    margin-top: 15px;
}

.carousel-catalog-inputs label>input[type="radio"]+* {
    display: inline-block;
    margin-right: 15px;
    font-size: 0;
}

.carousel-inputs{
    display: flex;
    justify-content: space-between;
}

.carousel-inputs #left-arrow,
.carousel-inputs #right-arrow{
    border: none;
    background: none;
    height: 60px;
}

